* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  height: 50px;
  background-color: #212121;
  z-index: 2;
  width: 100%; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 90px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo img {
    width: 70%;
    height: 35px;
    margin-top: 20px; }

  .subheader_official {
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }

  .subheader_phone {
    color: #ffffff;
    font-size: 22px;
    text-transform: uppercase;
    text-align: right; }

  .subheader_call {
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    margin-top: 19px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url('../img/main_bg.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #ffffff;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  .promo_subheader {
    color: #ffffff;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 36px;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }

.label {
  margin: 0 auto;
  margin-top: 50px;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url("../img/mobile_app.png") center (center / cover) no-repeat;

  .row {
    margin-top: 32px; }

  .mobile_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px; } }

.mobile_item_1 {
  background: url('../img/mobile_screen_1.jpg') center (center / cover) no-repeat; }

.mobile_item_2 {
  background: url('../img/mobile_screen_2.jpg') center (center / cover) no-repeat; }

.mobile_item_3 {
  background: url('../img/mobile_screen_3.jpg') center (center / cover) no-repeat; }

.mobile_item_4 {
  background: url('../img/mobile_screen_4.jpg') center (center / cover) no-repeat; }

.mobile_item_5 {
  background: url('../img/mobile_screen_5.jpg') center (center / cover) no-repeat; }

.mobile_item_6 {
  background: url('../img/mobile_screen_6.jpg') center (center / cover) no-repeat; }

.mobile_item_subtitle {
  color: #ffffff;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center; }

.mobile_item_plus {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 35px;
  left: 50%;
  transform: translateX(-50%);
  width: 26px;
  height: 26px;
  background-color: #000000;
  margin: 0 auto;
  border-radius: 100%;
  cursor: pointer; }

.choise {
  position: relative;
  min-height: 895px; }

.black {
  position: absolute;
  top: 0;
  left: 0;
  height: 499px;
  width: 100%;
  background-color: #070716;
  z-index: 1; }

.choise_img {
  position: relative;
  z-index: 2;
  display: block;
  margin: 56px auto 0 auto; }

.label_white {
  position: relative;
  top: 50px;
  z-index: 2;
  background-color: #ffffff;
  color: #000000;
  font-weight: 700; }

.title_white {
  position: relative;
  top: 30px;
  z-index: 2;
  color: #ffffff;
  letter-spacing: 5px; }

.subtitle {
  position: relative;
  top: 20px;
  z-index: 2;
  color: #ffffff;
  text-align: center; }

.choice_descr {
  text-align: center;
  color: #222222;
  font-size: 17px;
  font-weight: 300;
  line-height: 24px; }
